import uniPagination from '@/components/uni-pagination/uni-pagination.vue'//分页组件
export default {
	components: {uniPagination},
	data() {
		return {
			// 通行记录集合
			dataList:"",
			pageSize:20,//每页显示的数据量
			total:1,//总页数
			currentPage:1,//当前页数
			canFetch:false,// 判断是否有数据，点击下一页时会判断
		};
	},
	onLoad() {
		// 初始加载通行记录
		this.lodaRecord();
	},
	methods: {
		//初始加载通行记录
		async lodaRecord(){
			uni.showLoading({
				mask:true,
			    title: "正在加载中"
			});
			// 增加分页参数
			let data = {
				openId:this.$store.state.openId,
				limit : this.pageSize,
				page : this.currentPage
			}
			let result = await this.$request.post(this.$api.getMyRegisterLog,data);
			// 
			if(result.appCode == 1){
				this.dataList = result.data.records;
				this.total = result.data.total;
				this.canFetch = true;
			}else{
				this.$refs['Message'].error(result.appMsg);
			}
			uni.hideLoading();
		},
		// 点击分页
		jumpPage(e){
			// 如果有数据
			if(this.canFetch){
				this.canFetch = false;
				// 当前页码
				this.currentPage = e.current;
				// 初始加载通行记录
				this.lodaRecord();
			}
		}
		
	}
}
